import React, { Component } from 'react';
import DetallePartido from '../../Components/DetallePartido/DetallePartido';
import { createInfo,createInfo_finalizados,createInfo_espera } from '../../Utils/Utils';


import './PartidosContainer.css';

class PartidosContainer extends Component {

    constructor(props) {
        super(props)
        this.state = {mostrar:'true'}
     
       
    }
    componentWillMount() {
       
      
       this.updateData();
  }

  componentDidMount() {
    this.timerID = setInterval(
      () => this.updateData(),
      5000
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }
  updateData = () => {

    fetch('http://gmatchapp.com/api/v1/partidos')
    .then((response) => {
      return response.json()
    })
    .then((data) => {
      this.setState({ Partidos: data })
    })
}

handleClick = (id) => {
console.log(id)
/*
  let {mostrar}= this.state;

  if (mostrar){
      mostrar=false;
  }else{
      mostrar=true;
  }

  this.setState({mostrar : mostrar});
 */
}
    render(){
      const {mostrar} = this.state;
        let infoTorneos=[];
        const {Torneos} = this.props;

        const {Partidos} =this.state;
        if(Partidos && Torneos){
            infoTorneos=createInfo(this.state.Partidos,Torneos);
          
        }




        return (
            
            <div className="PartidosContainer">

            {infoTorneos && infoTorneos.map(((infoTorneo,index) =>
             
                <div key={index.toString()} className="torneo_grande" id={infoTorneo.nombre} onClick={this.handleClick(infoTorneo.nombre)}>
                  <div  >
                    {/*<div className="genero">{infoTorneo.genero}</div>*/}
                    <div className="flex">
                        <h2>{`${infoTorneo.nombre}`}</h2>
                    </div>
                  </div>
  
                  <div id={infoTorneo.nombre} className="infoTorneo">
                    
                    
                  {infoTorneo.partidos && infoTorneo.partidos.map((partido,index)=>
                    
                    <DetallePartido Partidos={partido}  key={index.toString()} onClick={this.props.onClick} Mostrar={this.handleClick}/>
                    
                  )}
                  </div>
                  
                </div>)
            )}    
        </div>
        );		

}
}
export default PartidosContainer;